body {
  margin: 0;
  padding: 0;
  background: #F2F4F5;
  color: #1E1E1F;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}

.TodoTittle {
    font-size: 34px;
    text-align: center;
    margin: 0;
    padding: 48px;
    font-weight: normal;
  }

.TodoTittle span {
  font-weight: bold;
}
.TodoSearch {
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family:'Montserrat' ;
    font-weight: 400;
}

.TodoSearch::placeholder {
    color: #A5A5A5;
    font-family:'Montserrat' ;
    font-weight: 400;
}

.TodoSearch:focus {
    outline-color: #61DAFA;
}

.TodoList {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
}
.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
}

.Icon-container-check {
    position: absolute;
    left: 12px;
}

.Icon-container-check--active {
    color: #4caf50;
}

.Icon-container-delete {
    position: absolute;
    top: -24px;
    right: 0;
}

.Icon-container-delete:hover {
    color: red;
}

.Icon-svg {
    width: 24px;
    height: 24px;
}

.Icon-container-check:hover .Icon-svg {
    fill: green;
}

.Icon-container-delete:hover .Icon-svg {
    fill: red;
}

.TodoItem {
    background-color: #fafafa;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0 5px 50px rgba(32, 35, 41, .15);
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete {
    text-decoration: line-through;
}


.TodoButton {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 1; /* Por encima de todo */


    -webkit-transform: rotate(0);


            transform: rotate(0);
    transition: .3s ease;
}

.TodoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
}
.todos-loading {
    margin-top: 20px;
    text-align: center;
  }
  
  .todo-skeleton {
    width: 100%;
    height: 50px;
    margin-bottom: 10px;
    background-color: #e0e0e0; /* Color gris */
    border-radius: 4px;
    -webkit-animation: color-animation 1s infinite alternate;
            animation: color-animation 1s infinite alternate;
  }
  
  @-webkit-keyframes color-animation {
    0% {
      background-color: #61DAFA; /* Color gris */
    }
    50% {
      background-color: #e0e0e0; /* Color azul */
    }
    100% {
      background-color: #e0e0e0; /* Color gris */
    }
  }
  
  @keyframes color-animation {
    0% {
      background-color: #61DAFA; /* Color gris */
    }
    50% {
      background-color: #e0e0e0; /* Color azul */
    }
    100% {
      background-color: #e0e0e0; /* Color gris */
    }
  }
  
  .loading-message {
    font-size: 20px; /* Tama침o de letra m치s grande */
    color: #61DAFA; /* Color azul */
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .loading-text {
    margin-right: 5px;
    -webkit-animation: text-animation 1s infinite alternate;
            animation: text-animation 1s infinite alternate;
  }
  
  .loading-dot {
    width: 8px;
    height: 8px;
    margin-left: 20px; /* Margen derecho para separar del texto */
    background-color: #61DAFA; /* Color azul */
    border-radius: 50%;
    -webkit-animation: dot-animation 1.5s infinite;
            animation: dot-animation 1.5s infinite;
    }
  
  @-webkit-keyframes text-animation {
    0% {
      color: #61DAFA; /* Color azul */
    }
    50% {
      color: #e0e0e0; /* Color gris */
    }
    100% {
      color: #e0e0e0; /* Color azul */
    }
  }
  
  @keyframes text-animation {
    0% {
      color: #61DAFA; /* Color azul */
    }
    50% {
      color: #e0e0e0; /* Color gris */
    }
    100% {
      color: #e0e0e0; /* Color azul */
    }
  }
  
  @-webkit-keyframes dot-animation {
    0% {
      -webkit-transform: translateX(-20px) scale(0.6);
              transform: translateX(-20px) scale(0.6);
      color: #61DAFA; /* Color azul */
    }
    50% {
      -webkit-transform: translateX(0) scale(1);
              transform: translateX(0) scale(1);
      color: #e0e0e0; /* Color gris */
    }
    100% {
      -webkit-transform: translateX(20px) scale(0.6);
              transform: translateX(20px) scale(0.6);
      color: #e0e0e0; /* Color azul */
    }
  }
  
  @keyframes dot-animation {
    0% {
      -webkit-transform: translateX(-20px) scale(0.6);
              transform: translateX(-20px) scale(0.6);
      color: #61DAFA; /* Color azul */
    }
    50% {
      -webkit-transform: translateX(0) scale(1);
              transform: translateX(0) scale(1);
      color: #e0e0e0; /* Color gris */
    }
    100% {
      -webkit-transform: translateX(20px) scale(0.6);
              transform: translateX(20px) scale(0.6);
      color: #e0e0e0; /* Color azul */
    }
  }
.ModalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Fondo transparente */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  

 
  

  
form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button--add {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }
  
  .TodoForm-button--cancel {
    background: transparent;
  }
